import styled from "@emotion/styled/macro";

export const Wrapper = styled.div`
  display: inline-block;
  padding: 10px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid #ba55d3;
`;

export const Message = styled.p`
  font-size: 14px;
  color: #ba55d3;
`;